/* ====================
  Module/Welcome
  ===================== */
.align--center {
  text-align: center;
}

.logo {
  margin-top: 115px;
  width: 161px;
}

.slogan {

  font-weight: normal;
  font-size: 1.25rem;
  margin-top: 25px;
  text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.1);
}

.btn {
  @include bounceIn(
    $duration: 1s,
    $count: 1,
    $delay: 1.4s,
    $function: ease,
    $fill: both
  );
  font-weight: bold;
  margin-top: 40px;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  padding: 13px 30px;
  color: #f5617d;
  border-radius: 3px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);

    /* Rectangle 8: */
  background-image: linear-gradient(-180deg, #FFFFFF 0%, rgba(255,255,255,0.80) 100%);
  box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.10);
  border-radius: 3px;

}

.description, .imprint, .contact {
  display: inline-block;
  margin-top: 90px;
  font-size: .875rem;
}

.imprint, .contact {
  margin-left: 30px;
}


.screen {
  display: flex;
  height: auto;
  @include slideInUp(
    $duration: 1.2s,
    $count: 1,
    $delay: .2s,
    $function: ease,
    $fill: both
  );


  &-taskana {
    margin: 0 auto;
    max-width: 1140px;
    width: 100%;
    height: 100%;
  }
}

.right--align {
  text-align: right;
}


// .right--align {
//   justify-content: flex-end;
// }

html {
  height: 100%;
}

a, a:visited, a:active, a:hover  {
  text-decoration: none;
  color: #fff;
}

.container {
  @include slideInDown(
    $duration: 1.2s,
    $count: 1,
    $delay: .2s,
    $function: ease,
    $fill: both
  );
}

.opnsource {
color: rgba(255, 255, 255, 0.8);
}
